.list-icons {
	width: 100%;
	max-width: 10.625em;
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: space-between;
}

.list-icons__circle {
	width: 2em;
	height: 2em;
	border-radius: 50%;
	background: var(--color-gray-600);
	display: flex;
	justify-content: center;
	align-items: center;
	cursor: pointer;
}

.list-icons__button {
	border: none;
	background: transparent;
}

.list-icons__icon {
	font-size: 1.2em;
	color: var(--color-gray-700);
	justify-self: center;
	align-self: center;
}

.list-icons__icon:hover {
	font-size: 1.4em;
}

/* Modifiers */
.list-icons__small {
	max-width: 9em;
}

.list-icons__small .list-icons__circle {
	width: 1.5em;
	height: 1.5em;
	background: var(--color-gray-800);
}

.list-icons__small .list-icons__icon {
	font-size: 0.8em;
	color: var(--color-white);
}

.list-icons__small .list-icons__icon:hover {
	font-size: 1em;
}

:root {
	--h-figure: 75%;
	--w-figure: 25%;
}

.logo__list-item-1,
.logo__list-item-3 {
	width: 25%;
	width: var(--w-figure);
	height: 75%;
	height: var(--h-figure);
}

.logo__list-item-2,
.logo__list-item-4 {
	width: 75%;
	width: var(--h-figure);
	height: 25%;
	height: var(--w-figure);
}

.logo {
	height: 1.25em;
	min-width: 2em;
	position: relative;
	margin-right: 0.625em;
}

.logo__list-item {
	border-top-left-radius: 50%;
	border-bottom-right-radius: 50%;
	position: absolute;
}

.logo__list-item-2 {
	bottom: 0;
	left: 0;
	background: var(--color-turquoise);
}

.logo__list-item-4 {
	top: 0;
	right: 0;
	background: var(--color-purple);
}

.navbar,
.navbar__list-up,
.navbar__brand {
	width: 100%;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
}

.navbar__checkbox-hide,
.navbar__label-hide {
	display: none;
}

.navbar {
	box-shadow: 0px 1px 0px #e5e9f2;
	transition: all ease 1s;
}

.navbar__brand {
	justify-content: flex-start;
	align-items: center;
	line-height: 1.25em;
}

.navbar__list-up {
	justify-content: flex-end;
}

.navbar__list {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	grid-gap: 3em;
	gap: 3em;
}

.navbar__list-item {
	max-width: 6em;
	text-align: center;
}

.navbar__link {
	cursor: pointer;
	font-weight: 500;
	font-size: var(--font-body-2);
	line-height: var(--font-body-2);
	color: #18191f;
}

.navbar__link-brand {
	font-weight: 700;
}

/* Modifiers */
.navbar-sign {
	padding: 1em var(--margin-w);
	background: var(--color-gray-500);
}

.navbar-sign .navbar__list-up {
	display: flex;
	justify-content: flex-end;
}

.navbar-light {
	padding: 1.375em var(--margin-w);
	background: var(--color-white);
	position: sticky;
	top: 0;
	z-index: 1;
}

.navbar-light .navbar__list-up {
	justify-content: flex-start;
	align-items: center;
}

.navbar-light .navbar__list-item:hover {
	opacity: 0.8;
}

.navbar-light .active:not(.navbar__link-brand) {
	color: var(--color-purple);
}

/* MEDIAQUERIES */
@media screen and (max-width: 800px) {
	.navbar {
		flex-direction: column-reverse;
		padding: 0;
	}

	.navbar__list-up {
		justify-content: space-between;
		padding: 1em var(--margin-w);
	}

	.navbar__list-down {
		display: none;
		width: 100%;
	}

	.navbar__list {
		grid-gap: 0;
		gap: 0;
	}

	.navbar__list-item {
		min-width: 100%;
	}

	.navbar__link {
		display: block;
		font-size: var(--font-h6);
		color: var(--color-gray-900);
		padding: 1em;
		height: 100%;
	}

	.navbar__label-hide {
		display: block;
		cursor: pointer;
	}

	.navbar__label-hide i {
		font-size: 1.5em;
		line-height: 1.5em;
		color: var(--color-gray-800);
	}

	.navbar__list-down-visible {
		display: flex;
		flex-direction: column;
		width: 100%;
	}

	/* MODIFIERS */
	.navbar-light {
		flex-direction: column;
	}

	.navbar-light .navbar__list-up {
		justify-content: space-between;
	}

	.navbar-light .navbar__list-item:hover {
		opacity: 1;
		background: var(--color-gray-500);
	}
}

.button {
	width: -webkit-fit-content;
	width: -moz-fit-content;
	width: fit-content;
	min-width: 8.75em;
	padding: 0.75em 1.25em;
	border-radius: 0.375em;
	font-size: var(--font-body-1);
	font-weight: 500;
	cursor: pointer;
	text-align: center;
	transition: 0.5s linear all;
}

.button__icon {
	margin-left: 0.625em;
	font-weight: 500;
}

/* Modifiers */
.button__outline-light {
	color: var(--color-white);
	border: 1px solid var(--color-white);
}

.button__outline-light:hover {
	background: var(--color-turquoise);
	border: 1px solid transparent;
	box-shadow: 2px 2px 3px var(--color-gray-600);
}

.button__purple {
	border: none;
	background: var(--color-purple);
	color: var(--color-white);
}

.button__purple:hover {
	background: var(--color-purple-800);
	/* box-shadow: 0px 0px 3px 1px rgba(0, 0, 0, 0.8); */
}

.button__gray {
	border: none;
	background: var(--color-purple-900);
	color: var(--color-purple);
}

.button__gray:hover {
	background: var(--color-purple-800);
	color: var(--color-white);
}

.hero__heading,
.hero__panel-dark {
	display: flex;
}

.hero {
	display: grid;
	grid-template-columns: 1fr 1fr;
	grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
	min-height: 49.375em;
}

.hero__heading {
	max-width: 33.75em;
	max-height: 21.125em;
	padding-left: var(--margin-w);
	align-self: center;
	flex-direction: column;
	justify-content: space-between;
	grid-gap: 1.5em;
	gap: 1.5em;
	color: var(--color-gray-900);
}

.hero__title {
	line-height: 120%;
	font-size: var(--font-h1);
	font-weight: 800;
}

.hero__description {
	line-height: 2em;
	font-size: var(--font-lead-1);
	font-weight: 400;
}

.hero__panel-dark {
	justify-content: flex-end;
	transition: all ease 1s;
}

.hero__panel-dark-sub {
	width: 90%;
	background: var(--color-gray-900);
	position: relative;
}

.hero__image {
	position: absolute;
}

.hero__image:nth-child(1) {
	width: 8em;
	height: 8em;
	bottom: 29.75em;
	left: -4em;
}

.hero__image:nth-child(2) {
	width: 13em;
	height: 13em;
	bottom: 28.3125em;
	left: 16.8125em;
}

.hero__image:nth-child(3) {
	width: 7em;
	height: 7em;
	bottom: 14.625em;
	left: 5.9375em;
}

.hero__image:nth-child(4) {
	width: 9.5em;
	height: 9.5em;
	bottom: 8.25em;
	left: 17.0625em;
}

@media screen and (max-width: 960px) {
	.hero__panel-dark {
		font-size: 12px;
	}
}

@media screen and (max-width: 800px) {
	.hero {
		margin: 0;
		grid-template-columns: 1fr;
	}

	.hero__panel-dark {
		grid-row: 1/2;
		height: 31.25rem;
		font-size: 0.625rem;
		background: var(--color-gray-900);
	}

	.hero__panel-dark-sub {
		width: 70%;
		background: var(--color-gray-900);
	}

	.hero__heading {
		max-width: 100%;
		max-height: -webkit-fit-content;
		max-height: -moz-fit-content;
		max-height: fit-content;
		padding: var(--margin-w);
		text-align: center;
		align-items: center;
	}
}

@media screen and (max-width: 500px) {
	.hero__panel-dark {
		font-size: 8px;
		height: 25rem;
	}

	.hero__panel-dark-sub {
		width: 80%;
		height: 25rem;
		background: var(--color-gray-900);
	}
}

