.input {
	width: 100%;
	min-height: 6.875em;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
}

.textarea {
	min-height: 8.5em;
}

.input__label {
	margin-bottom: 0.5em;
	font-size: var(--font-body-1);
	font-weight: 500;
}

.input__box {
	width: 100%;
	border-radius: 8px;
	padding: 0.8em;
	border: none;
	resize: none;
}

.input__box:focus {
	outline: none;
}

.input__error {
	padding: 8px 0px;
	color: var(--color-gray-300);
	font-weight: 500;
	font-size: var(--font-label-small);
}

/* MODIFIERS */
.input-light .input__box,
.input-outline-purple .input__box {
	border: 1px solid var(--color-purple);
}

.input-light .input__label {
	color: var(--color-gray-500);
}

.input-outline-purple .input__label {
	color: var(--color-gray-900);
}

.input-outline-purple .input__error {
	color: var(--color-gray-800);
}

.form__title {
	/* color: var(--color-gray-500); */
	/* font-size: var(--font-h2); */
	font-size: var(--font-h6);
	font-weight: 600;
	text-align: center;
	width: 100%;
	margin: 1em 0;
}

.form {
	display: flex;
	flex-direction: column;
	grid-gap: 1em;
	gap: 1em;
}

.form__button-submit {
	align-self: flex-end;
}

.form__question {
	text-decoration: none;
	font-size: var(--font-label-small);
	color: var(--color-gray-500);
}

/* MODIFIERS */
#form__wrap-login,
#form__wrap-signup {
	width: 100%;
	max-width: 30em;
	min-height: 35rem;
	padding: 2.5em 1.875em;
	background: var(--color-black-900);
	border-radius: 5px;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	text-align: center;
}

#form__wrap-signup {
	width: 100%;
	max-width: none;
	padding: 2.5em;
}

#form__wrap-signup .form {
	display: grid;
	grid-template-columns: 1fr 1fr;
	grid-gap: 10px 40px;
	gap: 10px 40px;
}

#form__wrap-signup .form__button-submit {
	grid-column: 1/3;
}

#form__wrap-contact {
	padding: 2em;
	background: var(--color-white);
	grid-gap: 20px;
	gap: 20px;
}

@media screen and (max-width: 800px) {
	.login-alumno {
		grid-template-columns: 1fr;
		justify-items: center;
		text-align: center;
	}
}

.info {
	min-height: 100vh;
	width: 100%;
	padding: 5em var(--margin-w);
	background: var(--color-gray-900);
	display: grid;
	grid-template-columns: 1fr 1fr;
	align-items: center;
	grid-gap: 1.25em;
	gap: 1.25em;
}

.info__message {
	width: 100%;
	max-width: 9em;
	color: var(--color-white);
	font-size: var(--font-h3);
	line-height: var(--font-h4);
}

/* MEDIAQUERIES */
@media screen and (max-width: 800px) {
	.info {
		grid-template-columns: 1fr;
		justify-items: center;
		text-align: center;
	}
}

.login-alumno{
    min-height: 51.875em;
    padding: 90px var(--margin-w);
    background: var(--color-turquoise-100);
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-items: center;
    grid-gap: 1.25em;
    gap: 1.25em;
}

.login-alumno__content{
    max-width: 340px;
    display: flex;
    flex-direction: column;
    grid-gap: 0.625em;
    gap: 0.625em;
}

.login-alumno__title{
    font-size: var(--font-lead-1);
    font-weight: 500;
}

.login-alumno__message{
    font-size: var(--font-h5);
    font-weight: 700;
}

@media screen and (max-width: 800px) {
    .login-alumno{
        grid-template-columns: 1fr;
        justify-items: center;
        text-align: center;
    }
}
.card-link{
    width: 100%;
    max-width: 25em;
    min-height: 25em;
    padding: 2.5em 1.875em;
    background: var(--color-turquoise-800);
    border-radius: 20px;
    box-shadow: 3px 3px 5px var(--color-gray-800);  
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    grid-gap: 0.625em;
    gap: 0.625em;
}

.card-link__title{
    font-size: var(--font-h5);
    font-weight: 500;
}

.card-link__description{
    font-size: var(--font-body-2);
    font-weight: 500;
}

.card-link__img{
    text-align: center;
    display: block;
    width: 90%;
    height: 15.625em;
    object-fit: cover;
    border-radius: 20px;
}

/* HOVERS */
.card-link:hover{
    background: var(--color-turquoise);
    color: white;
}
.menu{
    min-height: 100vh;
    background: var(--color-gray-500);
}

.menu__title{
    padding: 1em var(--margin-w);
    text-align: center;
    background: var(--color-white);
    box-shadow: 0px 1px 0px #E5E9F2;
    font-size: var(--font-h4);
    font-weight: 500;
}

.menu__cards{
    padding: 1em var(--margin-w);
    display: flex;
    grid-gap: 1.25rem;
    gap: 1.25rem;
    justify-content: center;
}
.select{
    width: 100%;
    min-height: 100px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.select__label{
    margin-bottom: 0.5em;
    font-size: var(--font-body-1);
    font-weight: 500;
}

.select__box{
    width: 100%;
    border-radius: 8px;
    padding: .8em;
    /* border: none; */
}

.select__error{
    padding: 8px 0px;
    color: var(--color-gray-300);
