    font-size: var(--font-label-small);
}

/* Moderators */
.select-light .select__box,
.select-outline-purple .select__box{
    border: 1px solid var(--color-purple);
}

.select-light .select__label{
    color: var(--color-gray-500);
}

.select-outline-purple .select__label{
    color: var(--color-gray-900);
}

.select-outline-purple .select__error{
    color: var(--color-gray-800);
}
.signup-alumno{
    min-height: 51.875em;
