}

.alert {
	position: fixed;
	bottom: 20px;
	left: 50%;
	-webkit-transform: translateX(-50%);
	        transform: translateX(-50%);
	width: 400px;
	min-height: 60px;
	display: flex;
	justify-content: center;
	align-items: center;
	border-radius: 5px;
	z-index: 1000;
	transition: 0.4s ease-in-out all;
}

.alert__message {
	padding: 1em;
	color: whitesmoke;
	text-align: center;
	font-size: 1em;
	text-transform: uppercase;
}

/* --------------- MODIFIERS */
.alert-primary {
	background: var(--color-purple);
}

/* --------------------- MEDIAQUERIES */
@media screen and (max-width: 600px) {
	.alert {
		width: 300px;
	}
}

* {
	font-family: "Inter", sans-serif;
}

:root {
	--margin-w: 10em;

	--color-gray-100: #f4f5f7;
	--color-gray-300: #d9dbe1;
	--color-gray-500: #eeeff4;
	--color-gray-600: #d7d8de;
	--color-gray-700: #969bab;
	--color-gray-800: #474a57;
	--color-gray-900: #18191f;
	--color-black-900: #0b0d17;
	--color-purple: #8c30f5;
	--color-purple-800: #d6b1ff;
	--color-purple-900: #f1e4ff;
	--color-white: #ffffff;
	--color-turquoise: #2ec5ce;
	--color-turquoise-100: #d5fafc;
	--color-turquoise-800: #75e3ea;
	--color-orange: #fe9a22;
	--color-orange-800: #ffc278;
	--color-red: #f39f9f;

	--font-h1: 4.5em;
	--font-h2: 3em;
	--font-h3: 2.5em;
	--font-h4: 1.75em;
	--font-h5: 1.5em;
	--font-h6: 1.25em;
	--font-body-1: 1em;
	--font-body-2: 0.875em;
	--font-lead-1: 1.125em;
	--font-label-small: 0.75rem;

	--weight-500: 500;

	--line-h-text: 1.5em;
}

body {
	font-size: 16px;
}

.App-logo {
	height: 40vmin;
	background: red;
	pointer-events: none;
}

@media screen and (max-width: 1060px) {
	body {
		font-size: 14px;
		transition: all ease 1s;
	}
}

@media screen and (max-width: 960px) {
	:root {
		--margin-w: 6em;
	}
}

@media screen and (max-width: 800px) {
	:root {
		--margin-w: 4em;
	}

	body {
		font-size: 12px;
	}
}

